import React from "react";

export default function Experiences({ texts }) {
  if (!texts) return <div />;

  return (
    <div className="experience" id="experience">
      {/* experiências profissionais
   com link para algum pdf online com o currículo*/}
      <p className="text title">{texts.experience.title}</p>
      <p className="text">
        {texts.experience.content}{" "}
        <a href="https://www.w16.com.br/" target="_blank">
          W16 Software House
        </a>
        {texts.experience.content2}
      </p>
      <p className="text">{texts.experience.content3}</p>
    </div>
  );
}
