import Select from "react-select";

const selectStyles = {
  container: (provided, state) => ({
    ...provided,
    width: "80px",
  }),
  control: (provided, state) => ({
    ...provided,
    backgroundColor: "rgba(255, 255, 255, 0)",
    height: "45px",
    boxShadow: "none",
    border: 0,
  }),
  menu: (provided, state) => ({
    ...provided,
    backgroundColor: "#44475a",
    display: "flex",
    justifyContent: "center",
    width: "50px",
    boxShadow: "none",
    border: 0,
    marginTop: "-1px",
  }),
  valueContainer: (provided, state) => ({
    ...provided,
    height: "35px",
  }),
  option: (provided, state) => ({
    ...provided,
    backgroundColor: "rgba(255, 255, 255, 0)",
  }),
  dropdownIndicator: (provided, state) => ({
    ...provided,
    color: "#f8f8f2",
  }),
};

export default function Navbar({
  language,
  setLanguage,
  options,
  texts,
  windowWidth,
}) {
  if (!texts) return <div />;

  return (
    <div className="navbar">
      <h2>{texts.menu.title}</h2>
      {windowWidth > 800 && (
        <div className="options">
          <div>
            <a href="#introduction">{texts.menu.menuOptions[0]}</a>
          </div>
          <div>
            <a href="#experience">{texts.menu.menuOptions[1]}</a>
          </div>
          <div>
            <a href="#projects">{texts.menu.menuOptions[2]}</a>
          </div>
          <div>
            <a href="#knowledge">{texts.menu.menuOptions[3]}</a>
          </div>
          <div>
            <a href="#contact">{texts.menu.menuOptions[4]}</a>
          </div>
        </div>
      )}
      <div className="languages">
        <p>{texts.menu.menuOptions[5]}:</p>
        <Select
          defaultValue={language}
          onChange={setLanguage}
          options={options}
          isSearchable={false}
          styles={selectStyles}
        />
      </div>
    </div>
  );
}
