import React from "react";
import { MdAdd, MdBuild, MdHome, MdWork } from "react-icons/md";
import { GoBookmark } from "react-icons/go";
import { FaUser } from "react-icons/fa";

import { Fab, Action } from "react-tiny-fab";

const actionbutton = {
  backgroundColor: "#6272a4",
};

const menu = {
  backgroundColor: "#44475a",
};

export default function FloatingMenu({ texts }) {
  return (
    <Fab
      mainButtonStyles={menu}
      style={{ top: 0, left: 0 }}
      icon={<MdAdd size={30} />}
      alwaysShowTitle={true}
      event="click"
    >
      <Action style={actionbutton} text={texts.menu.menuOptions[0]}>
        <a href="#introduction">
          <MdHome size={25} />
        </a>
      </Action>

      <Action style={actionbutton} text={texts.menu.menuOptions[1]}>
        <a href="#experience">
          <GoBookmark size={25} />
        </a>
      </Action>

      <Action style={actionbutton} text={texts.menu.menuOptions[2]}>
        <a href="#projects">
          <MdWork size={25} />
        </a>
      </Action>

      <Action style={actionbutton} text={texts.menu.menuOptions[3]}>
        <a href="#knowledge">
          <MdBuild size={25} />
        </a>
      </Action>

      <Action style={actionbutton} text={texts.menu.menuOptions[4]}>
        <a href="#contact">
          <FaUser size={25} />
        </a>
      </Action>
    </Fab>
  );
}
