import React from "react";

import myPicture from "../assets/myPicture.png";

export default function Introduction({ texts }) {
  if (!texts) return <div />;

  return (
    <div className="introduction" id="introduction">
      <img src={myPicture} />
      <div className="profile">
        <p className="title text">{texts.introduction.title}</p>
        <p className="text">{texts.introduction.text}</p>
      </div>
    </div>
  );
}
