import React from "react";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from "react-responsive-carousel";

import caloteiros from "../assets/projects/caloteiros.png";
import ecommerce from "../assets/projects/ecommerce.png";
import github from "../assets/projects/github.png";
import gobarber from "../assets/projects/goBarber.png";
import journalist from "../assets/projects/journalist.jpg";
import moneyManager from "../assets/projects/moneyManager.png";
import toDo from "../assets/projects/toDo.png";

export default function Projects({ texts }) {
  if (!texts) return <div />;

  return (
    <div className="projects" id="projects">
      <p className="text title">{texts.projects.title}</p>
      <p className="text">{texts.projects.content}</p>

      <Carousel>
        <div>
          <img src={caloteiros} />
          <p className="legend">Caloteiros App</p>
        </div>
        <div>
          <img src={ecommerce} />
          <p className="legend">Rocketshoes</p>
        </div>
        <div>
          <img src={github} />
          <p className="legend">Github Api</p>
        </div>
        <div>
          <img src={gobarber} />
          <p className="legend">GoBarber</p>
        </div>
        <div>
          <img src={journalist} />
          <p className="legend">Journalist Breaking News</p>
        </div>
        <div>
          <img src={moneyManager} />
          <p className="legend">Money Manager</p>
        </div>
        <div>
          <img src={toDo} />
          <p className="legend">ToDo List</p>
        </div>
      </Carousel>
    </div>
  );
}
