import React from "react";
import { MdEmail, MdPhone, MdPlace } from "react-icons/md";
import { IoLogoLinkedin, IoLogoWhatsapp } from "react-icons/io";

export default function Contact({ texts }) {
  if (!texts) return <div />;

  return (
    <div className="contact" id="contact">
      <p className="text title">{texts.contact.title}</p>

      <div className="item">
        <MdEmail size={30} />
        <p>borelli.guilherme@gmail.com</p>
      </div>

      <div className="item">
        <a
          className="logo"
          href="https://www.linkedin.com/in/guilhermeborelli/"
          target="_blank"
        >
          <IoLogoLinkedin size={35} color={"#f1faee"} />
        </a>
        <p>{texts.contact.linkedin}</p>
      </div>

      <div className="item">
        <a className="logo" href="https://wa.me/+5548991509631" target="_blank">
          <IoLogoWhatsapp size={35} color={"#f1faee"} />
        </a>
        <p>{texts.contact.whatsapp}</p>
      </div>

      <div className="item">
        <MdPhone size={30} />
        <p>+55 48 99150-9631</p>
      </div>

      <div className="item">
        <MdPlace size={30} />
        <p>{texts.contact.localization}</p>
      </div>
    </div>
  );
}
